import PointObject from "./PointObject";
import vector from "./VectorMaths";

export default class FlatMirror extends PointObject {
    constructor(position, angle, length, f=0, mass=1) {
        super(position, mass);
        this.f = f;
        // this.angle = angle;
        this.length = length;
        const center = new vector(position);
        this.center = center;
        const offset = vector.polar(length/2, angle);
        this.mirrorDirection = offset.unitVector;
    };

    mirrorAsLine(t) {
        return(new vector({
            x: this.center.x+this.mirrorDirection.x*t, 
            y: this.center.y+this.mirrorDirection.y*t
            })
        )
    }

    setProperties(p) {
        p.strokeWeight(5);
        const c = p.color(162, 172, 188);
        p.fill(c);
        p.stroke(c);
    };

    referenceToPoint(r1) {
        const r0 = this.center;
        const d0 = this.mirrorDirection;
        const t = (-d0.x*r0.x - d0.y*r0.y + r1.x*d0.x + r1.y*d0.y)/(d0.x**2 + d0.y**2);
        return(this.mirrorAsLine(t));
    }

    reflect(r1) {
        const r1v = new vector(r1.position);
        const ref = this.referenceToPoint(r1.position);
        const reflection = ref.add(r1v.scale(-1)).add(ref);
        return(new PointObject(reflection.components))
    }

    reflectLine(r1) {
        const ref = this.referenceToPoint(r1);
        const reflection = ref.add(new vector(r1).scale(-1)).add(ref);
        return(reflection);

    }

    show(p) {
        this.setProperties(p);
        const p1 = this.mirrorAsLine(-this.length);
        const p2 = this.mirrorAsLine(this.length);
        p.line(p1.x, p1.y, p2.x, p2.y);
    };
};