import React, { Component } from "react";
import banana from '../images/Banana-Pudding.jpg' 
import '../App.css';

export class Pudding extends Component {
    render(){
        return(
        <div className='body'><div className="content">
            <h1>Banana Pudding</h1>
            <p>A very Southern dessert that I'm sure we've all enjoyed on a hot summer afternoon. It would always be a genuine surprise when the glass bowl was in the 1940s GE refrigerator in the living room. The simple dessert would never fail to bring me back to my childhood. It tastes light and carefree, and persuades the eater to daydream for a little while like he/she used to. Pudding brings us back.</p>
            <ul>
                <li>1 (5 ounce) package instant vanilla pudding mix</li>
                <li>1 (14 ounce) can sweetended condensed milk</li>
                <li>3 cups milk</li>
                <li>1 teaspoon vanilla extract</li>
                <li>1 (8 ounce) container frozen whipped topping, thawed</li>
                <li>4 bananas, sliced</li>
            </ul>
            <img src={banana} alt="banana-pudding" />
        </div>
    </div>)
    };
}