import React, { Component }from 'react';
import LBWrapper from '../sketches/LightBoxWrapper'
import PIapprox from '../sketches/PiApproximation'


export class AnimHome extends Component {
    constructor(props) {
        super(props);
        this.state = {
            sketchIndex:0,
            sketches: [LBWrapper, PIapprox]
        }
    }

    handleFButton(e) {
        this.setState({
            ...this.state, 
            sketchIndex: (this.state.sketchIndex+1)%this.state.sketches.length
        })
    }

    render() {
        let Sketch = this.state.sketches[this.state.sketchIndex]
        return (<div className='content'>
            <div>
                <p>--- Under Construction ---</p>
                Selected Sketch: {this.state.sketchIndex + 1}
                <button style={{'text-align':"right", "float": "left"}} className= 'button' onClick={(e) => this.handleFButton(e)}>Next Sketch</button>
                <Sketch/>
            </div>
            
        </div>)
    }
}