import React, {Component} from 'react';
import P5wrapper from 'react-p5-wrapper';

function sketch (p) {

    // declare variables

    let pi = 1
    for (let i=0; i<50; i++) {
        pi += ((-1)^i)/(2*i+1)
        console.log(pi)
    }
    p.setup = () => {
        p.createCanvas(600,400);
        // draw color
        p.strokeWeight(5);
        // draw width
        let c = p.color(255, 204, 0);
        p.fill(c);
        p.stroke(c);
    };
    console.log(pi)
    p.myCustomRedrawAccordingToNewPropsHandler = function (props) {
        
    };

    p.draw = () => {
        p.background(100);
        
        // draw code here
        p.text(pi)
    };
};


export default class PIapprox extends Component {
    render() {
        return(<div>
            <P5wrapper sketch={sketch}/>
        </div>)
    }
}