import FlatMirror from '../SimCore/FlatMirror'
import vector from '../SimCore/VectorMaths';
import PointObject from '../SimCore/PointObject'

export default function sketch (p) {

    // declare variables
    let c = {x:200,y:200};
    const origin = {x: c.x, y:c.y+75};
    let theta = 0;
    // let t = 0;
    // let output = [];
    p.setup = () => {
        p.createCanvas(600,400);
        // draw color
        
    };

    function box(center, s) {
        p.strokeWeight(2);
        // draw weight
        const c = p.color(0, 0, 0);
        p.fill(c);
        p.stroke(c);
        p.line(center.x + s/2, center.x - s/2, center.y + s/2, center.y + s/2);
        p.line(center.x + s/2, center.x - s/2, center.y - s/2, center.y - s/2);
        p.line(center.x + s/2, center.x + s/2, center.y - s/2, center.y + s/2);
        p.line(center.x - s/2, center.x + s/2, center.y - s/2, center.y - s/2);
        p.ellipse(center.x, center.y, 2,2);
    }

    function reflections(end) {
        const col = p.color(255, 204, 0);
        p.fill(col);
        p.stroke(col);
        p.line(c.x, c.y, origin.x, origin.y);
        p.line(c.x, c.y, end.x, end.y);

    }

    p.myCustomRedrawAccordingToNewPropsHandler = function (props) {
        
    };

    p.draw = () => {
        p.background(100);
        let m = new FlatMirror(c, theta, 40)        
        theta = theta - 0.01;
        // draw code here
        box(c, 150)
        let point = m.reflect(new PointObject(origin));
        let l2 = new vector(point.position);
        //point.show(p)
        l2 = l2.scale(-1).add(new vector(c)).scale(2).add(new vector(c))
        reflections(l2)
        m.show(p);
        
    };
};