export default class vector {
    // I can imagine writing this with some kind of loop for n dimensions
    constructor(components) {
        this.x = components.x;
        this.y = components.y;
        if (components.z) {
            this.z = components.z;
        } else {
            this.z = 0;
        };
    };

    static polar(magnitude, angle) {
        return(new vector({x: magnitude*Math.cos(angle), y: magnitude*Math.sin(angle)}))
    };

    add(v) {
        const i = this.x + v.x;
        const j = this.y + v.y;
        return(new vector({x:i,y:j}));
    };

    scale(k) {
        return(new vector({x:k*this.x, y:k*this.y}));
    };

    rotate(theta) {
        return(new vector({
            x: this.x*Math.cos(theta) - this.y*Math.sin(theta),
            y: this.x*Math.sin(theta) + this.y*Math.cos(theta)    
        }))
    }

    get magnitude() {
        return(Math.sqrt(this.x**2 + this.y**2 + this.z**2))
    };

    get unitVector() {
        return(this.scale(1/this.magnitude));
    };

    get componentsAr() {
        return([this.x, this.y]);
    };

    get components() {
        return({x:this.x, y: this.y});
    };
}