export default class PointObject {
    constructor(p, v = {x:0, y:0}, m = 1, color = [247, 244, 54]) {
        this.position = {x: p.x, y: p.y};
        this.velocity = v;
        this.mass = m;
        this.color = color;
    };

    progress(t) {
        const {x,y} = this.position;
        this.position.x = x + this.velocity.x*t;
        this.position.y = y + this.velocity.y*t;
    };

    setProperties(p) {
        const c = p.color(...this.color);
        p.fill(c);
        p.stroke(c);        
    };

    show(p) {
        this.setProperties(p);
        p.ellipse(this.position.x, this.position.y, 5, 5);
    }
};